const signupBtnsEl = document.querySelectorAll('[data-modal-open]'); //  ""
const signupModal = document.querySelector('[data-modal-show]'); //   
const closeBtn = signupModal.querySelector('[data-modal-close]'); //    

const registrationFormWrapper = document.querySelector('.regisraion-wrapper'); //   
const registrationLink = document.querySelector('.registration-link'); //    

const loginFormWrapper = document.querySelector('.login-form-wrapper'); //   
const loginLink = document.querySelector('.login-link'); //    

// const userSignDesc = document.querySelector('.user-sign');

//     
export function closeModal() {
  signupModal.style.display = 'none';
  closeBtn.removeEventListener('click', closeModal);
  window.removeEventListener('click', outsideClick);
  document.removeEventListener('keydown', escapePress);
}

//     ""
signupBtnsEl.forEach(btn => {
  btn.addEventListener('click', onOpenModalSignClick);
});

function onOpenModalSignClick(e) {
  e.preventDefault();
  signupModal.style.display = 'block';
  closeBtn.addEventListener('click', closeModal);
  window.addEventListener('click', outsideClick);
  document.addEventListener('keydown', escapePress);
}

//       
function outsideClick(event) {
  if (event.target === signupModal) {
    closeModal();
  }
}

//      Escape
function escapePress(event) {
  if (event.key === 'Escape' && signupModal.style.display === 'block') {
    closeModal();
  }
  document.removeEventListener('keydown', escapePress);
}

//       
loginLink.addEventListener('click', () => {
  loginFormWrapper.classList.remove('hidden'); //   
  registrationFormWrapper.classList.add('hidden'); //   
  loginLink.classList.add('active'); //   "active"     
  registrationLink.classList.remove('active'); //   "active"     
});

//       
registrationLink.addEventListener('click', () => {
  registrationFormWrapper.classList.remove('hidden'); //   
  loginFormWrapper.classList.add('hidden'); //   
  registrationLink.classList.add('active'); //   "active"     
  loginLink.classList.remove('active'); //   "active"       
});

document.querySelector('.user-sign').addEventListener('click', onLogoutClick);

function onLogoutClick(e) {
  console.log(e);
  // document.querySelector('.log-out-btn').style.display = 'flex';
  document.querySelector('.log-out-btn').classList.toggle('is-open');
  document.querySelector('.user-sign__icon-down').classList.toggle('rotation');
}
